.press {
    overflow: hidden;
    &__container {
        padding-top: 60px;
        padding-bottom: 60px; }
    &__title {
        padding-bottom: 66px; }

    &__slider-w {
        position: relative; }

    &__slider {
        margin-right: -35px;
        margin-left: -35px; }

    // &__slide

    &__slide-w {
        padding-right: 35px;
        padding-left: 35px; }
    &__date {
        margin-bottom: 9px;
        color: #1b1e64;
        font-size: 16px;
        font-weight: 300;
        line-height: 1; }

    &__slide-title {
        // margin-bottom: 17px
        margin-bottom: 21px;
        padding-right: 20px;
        font-size: 26px;
        font-weight: 500;
        line-height: 1.523; }
    &__link {
        text-decoration: none;
        color: #000000;
        @include tr(.3s ease);
        &:hover {
            color: lighten(#000000, 10);
            @include tr(.3s ease); } }
    &__text {
        margin-bottom: 32px;
        font-size: 18px;
        font-weight: 400;
        line-height: 1.706; }
    &__more {
        color: #1b1e64;
        font-size: 16px;
        font-weight: 500;
        line-height: 1;
        text-decoration: none;
        @include tr(.3s ease);
        &:hover {
            color: lighten(#1b1e64, 10);
            @include tr(.3s ease); } }



    &__arrow {
        @include resetbutton();
        position: absolute;
        top: calc(50% - 62px);
        transform: translateY(-50%);
        @include tr(.3s ease);
        &:hover {
            opacity: .8;
            @include tr(.3s ease); } }
    &__arrow_back {
        left: -72px; }
    &__arrow_next {
        right: -80px;
        transform: translateY(-50%) rotate(180deg); }

    .slick-dotted {
        &.slick-slider {
            margin-bottom: 0;
            padding-bottom: 82px; } }

    .slick-dots {
        bottom: 0;
        font-size: 0;
        li {
            margin: 0;
            margin-right: 11px;
            width: 16px;
            height: 15px;
            padding: 0;
            cursor: pointer;
            &.slick-active {
                button {
                    background-color: #1b1e64; } }

            &:last-child {
                margin-right: 0; } }
        button {
            width: 16px;
            height: 15px;
            background-color: rgba(27, 30, 100, 0.1);
            border-radius: 50%;
            @include tr(.3s ease);

            &::before {
                display: none; }
            &:hover {
                background-color: #1b1e64;
                @include tr(.3s ease); } } } }
