.footer {
  background-color: #1b1e64;
  overflow: hidden;
  &__container {
    padding-top: 43px;
    padding-bottom: 20px;
    flex-wrap: wrap; }

  &__col {
    width: 100%;
    &:nth-child(1) {
      max-width: 360px; }
    &:nth-child(2) {
      max-width: 490px; }
    &:nth-child(3) {
      margin-left: auto;
      margin-right: -11px;
      max-width: 100px; } }

  &__nav {
    padding-top: 2px; }

  &__nav-sec {
    padding-top: 4px;
 }    // margin-right: -20px

  &__copyright {
    color: rgba(255, 255, 255, 0.6);
    font-size: 14px;
    font-weight: 400;
    line-height: 1.071; } }
