.global {
    background-color: #f7f7f8;
    overflow: hidden;
    &__container {
        padding-top: 60px;
        padding-bottom: 35px; }

    &__title {
        margin-bottom: 45px; }

    // &__row

    &__col {
        max-width: 430px;
        width: 100%;
        flex-grow: 1;
        flex-shrink: 1;
        &:last-child {
            max-width: 670px;
            width: 100%;
            .global__langs {
                margin-right: -260px;
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap; }
            .global__lang {
                flex-grow: 1;
                max-width: 50%;
                flex-basis: auto;
                width: 100%;
                &:nth-child(1) {
                    order: 0; }
                &:nth-child(2) {
                    order: 1; }
                &:nth-child(3) {
                    order: 2; }
                &:nth-child(4) {
                    order: 3; }
                &:nth-child(5) {
                    order: 4; }
                &:nth-child(6) {
                    order: 5; } } } }

    &__col-title {
        margin-bottom: 35px;
        color: rgba(27, 30, 100, 0.2);
        font-size: 40px;
        font-weight: 700;
        line-height: 1; }

    // &__langs

    &__lang {
        margin-bottom: 20px;
        align-items: center; }

    // &__icon

    &__lang-title {
        padding-left: 18px;
        color: #575757;
        font-size: 22px;
        font-weight: 500; } }
